<template>
<div id="QuestionnareList" v-loading="isLoading"    element-loading-text="">
    <el-row :gutter="5" class="center_row">
        <!-- <el-col  :span="3">
            <el-dropdown>
                <el-button type="primary"><i class="el-icon-arrow-down el-icon--right"></i></el-button>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item></el-dropdown-item>
                    <el-dropdown-item></el-dropdown-item>
                    <el-dropdown-item></el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </el-col>
        <el-col  :span="3">
            <el-dropdown>
                <el-button type="primary"><i class="el-icon-arrow-down el-icon--right"></i></el-button>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item></el-dropdown-item>
                    <el-dropdown-item></el-dropdown-item>
                    <el-dropdown-item></el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </el-col> -->
        <el-col :span="3">
            <el-button type="primary" class="addSurvey" icon="el-icon-plus" @click="addSurvey();routerLink(pathItems[0].path,1)" ></el-button>
        </el-col>
        <el-col :span="3">
            <el-button type="primary" class="addSurvey" icon="el-icon-error" @click="deleteSurvey()" ></el-button>
        </el-col>
    </el-row> 
    <el-row >
        <el-col>
            <el-table :data="tableData.message.list" ref="tableData.message.list" @selection-change="handleSelectionChange" stripe >
               <el-table-column  type="selection" width="55"></el-table-column>
               <el-table-column label="" prop='title' width="200"></el-table-column>
               <el-table-column label="" prop='templateType'  width="100"></el-table-column>
               <el-table-column label="" prop='categoryIds'  width="100"></el-table-column>
               <el-table-column label="" :filter-method="filterHandler" :filters="FormSort.StatusSort" prop='status'  width="100"><template slot-scope="scope"><i class="el-icon-success" v-if=" scope.row.status == ''"></i><i class="el-icon-info" v-if="scope.row.status == ''"></i><span style="margin-left: 10px">{{ scope.row.status }}</span></template></el-table-column>
               <el-table-column label="" prop='Received_data'  width="100"></el-table-column>
               <el-table-column label="" :filter-method="filterHandler" :filters="FormSort.DataSort" sortable width="150" prop='createTime'><template slot-scope="scope"><i class="el-icon-time"></i><span style="margin-left: 10px">{{ scope.row.createTime }}</span></template></el-table-column>
               <el-table-column label="" sortable width="150" prop="updateTime"><template slot-scope="scope"><i class="el-icon-time"></i><span style="margin-left: 10px">{{ scope.row.updateTime }}</span></template></el-table-column>
               <el-table-column label="" width=""><template slot-scope="scope"><el-button size="mini" @click="handleEdit(scope.$index, scope.row);routerLink(pathItems[0].path,1)"></el-button><el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button><el-button size="mini" type="info"></el-button></template></el-table-column>                                   
            </el-table>
        </el-col>
    </el-row>
    <el-row class="pagenation">
        <el-col :span="6" :offset="6">
            <el-pagination :total="tableData.message.total" :current-page.sync="currentPage" :size="tableData.message.pageSize" background :pager-count="7" :page-size="10" :page-count="tableData.message.pages" layout="total, prev, pager, next,jumper"  @current-change="handleCurrentChange"></el-pagination>
        </el-col>
    </el-row>
</div>
</template>


<script>
import * as Survy_Api from '@/Questionnaire_interface/api.js'
import Qs from 'qs'
//Vue.prototype.$Survy_Api = Survy_Api  //vue
export default {
    data(){
        return{
          currentPage:1,
          PreBatchDeletion:[],
          items:[],     //
          BatchDeletion:[],
          FormSort:{
            StatusSort:[{
              text:"",
              value:""
            },
            {
              text:"",
              value:"",
            }],
            DataSort:[],
          },
		      isLoading:false,//
          tableData:{message:{
            pageSize:5,
            list:[{
              title:'',
              templateType:'x',
              condition:'',             
              Received_data:'0',
              createTime:'2018-1-20',
              updateTime:'2018-1-20',              
          },
          {
              title:'',
              templateType:'x',
              condition:'',             
              Received_data:'0',
              createTime:'2018-1-20',
              updateTime:'2018-1-20',            
              
          }]}},
          pathItems:[     //
            {
              path:"/exhibition/questionnare/questionnareEditing"
            },
          ]  
        }
    },
    methods:{
        addSurvey(){
            let that =this;
            console.log(Survy_Api.default.addSurvey);
            let data = {"title":"","remark":"","templateType":0,"status":0,"categoryIds":[1],"questions":[]};  //json
            /*  var   gettype=Object.prototype.toString;
            console.log(gettype.call(data)); */
            //data = JSON.stringify(data);
            /* console.log(data);
            console.log(gettype.call(data)); */
            localStorage.setItem("XSurveyChange","");
            Survy_Api.default.addSurvey(that,data);   //localstorage
            localStorage.setItem("XSurveyInformation",JSON.stringify(data));
            /*  const loading = this.$loading({ 
                lock: true, 
                text: '', 
                spinner: 'el-icon-loading', 
                background: 'rgba(0, 0, 0, 0.7)', 
                target: document.querySelector('.div1') 
              }); 
              setTimeout(() => {
                loading.close();
              }, 2000); */
              
        },
        routerLink(path,id){
          var time = false;
            setTimeout(() => {
               this.$router.push({
                path:path,
               /*  query:{
                   id:id,
                } */
                })
              }, 2000);  
              },
              init(){
                let data = JSON.parse(window.localStorage.getItem("CurrentSurveyListPage"));
                //data = {"userId":0};   //id
                this.currentPage = JSON.parse(window.localStorage.getItem("CurrentSurveyListPage")).pageNum;
                data = Qs.stringify(data); 
                let that =  this;
                Survy_Api.default.search(that,data);
              },
              handleCurrentChange(val){  //
                let that =this;
                let val_tostring = val.toString();
                let data = {"userId":0,"pageNum":val};
                window.localStorage.setItem("CurrentSurveyListPage",JSON.stringify(data));
                data = Qs.stringify(data);   //
                //window.localStorage.setItem("CurrentSurveyListPage",data);   //
                Survy_Api.default.search(that,data);
                this.currentPage = JSON.parse(window.localStorage.getItem("CurrentSurveyListPage")).pageNum;
              },
              handleDelete(index, row){
                let that =this;
                //let val_tostring = row.id.toString();
                let data = {"ids":""};
                let deleteArray = [row.id];
                data.ids = deleteArray;
                console.log(data);
                data = Qs.stringify(data);
                console.log(row.id);
                this.$confirm('','',{
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type:'warning'
                }).then(()=>{
                  Survy_Api.default.deleteSurvey(that,data);
                  Survy_Api.default.search(that,window.localStorage.getItem("CurrentSurveyListPage"));
                  this.currentPage = window.localStorage.getItem("CurrentSurveyListPage").pageNum;
                }).catch(()=>{
                  that.$message({
                    type:'info',
                    message:'!'
                  })
                })
              },
              handleEdit(index,row){
                let that = this;
                let data ={"id" : row.id,};  //id
                const loading = this.$loading({ 
                  lock: true, 
                  text: '', 
                  spinner: 'el-icon-loading', 
                  background: 'rgba(0, 0, 0, 0.7)', 
                  target: document.querySelector('.div1') 
                }); 
                Survy_Api.default.getSurveyInfo(that,data);
                localStorage.setItem("XSurveyChange","");
                setTimeout(() => {
                  loading.close();
                }, 2000); 
                
              },
              filterHandler(value, row, column) {     //
                const property = column['property'];
                return row[property] === value;
              },
              handleSelectionChange(val){  //form
                this.PreBatchDeletion = val; 
              },
              deleteSurvey(){   //
              let that = this;
                //console.log(this.BatchDeletion);
                this.$confirm('','',{
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type:'warning'
                }).then(()=>{
                  if(this.PreBatchDeletion.length == 0){
                     that.$message({
                        type:'error',
                        message:'!'
                      })
                  }else{
                    for(let i =0;i < this.PreBatchDeletion.length;i++){
                      this.BatchDeletion.push(this.PreBatchDeletion[i].id);
                    }
                    let data = {"ids":this.BatchDeletion};
                    console.log(data);
                    data = Qs.stringify(data);
                  Survy_Api.default.deleteSurvey(that,data);
                  this.BatchDeletion = [];  //id
                  this.PreBatchDeletion = []; //id
                  Survy_Api.default.search(that, window.localStorage.getItem("CurrentSurveyListPage"));
                  }
                }).catch(()=>{
                  that.$message({
                    type:'info',
                    message:'!'
                  })
                })
               
              }
        },
        activated(){
          this.init();
        },
        watch:{
                
            }
}
</script>


<style scoped lang="scss">
  #QuestionnareList{
    background: rgba(255, 255, 255, 0.95);
  }
  .el-dropdown {
    vertical-align: top;
  }
.el-dropdown + .el-dropdown {
    margin-left: 15px;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .el-row{
      
  }
  .center_row{  //
      margin:10px 0 0 0!important; 
  }
  .pagenation{
      background: white;
      padding:5px;
      
  }
  .addSurvey{
      margin:10px 8px;
  } 
</style>


