<template>
<div id="QuestionnareEditing">
   <el-row >
        <el-col :span="colwidth" :class="searchBarFixed == true ? 'isFixed' :''" id="MenuList">
        <div class="Istoggle" @click="function(){isCollapse = !isCollapse;if(colwidth==4){colwidth = 1;ColOffset=1;}else{colwidth = 4;ColOffset=4;}}" >
            <i class="el-icon-arrow-down" v-show="!isCollapse"></i>
            <i class="el-icon-more" v-show="isCollapse"></i>
        </div>
        <el-menu :collapse="isCollapse" >
            <el-submenu index='1'>
                <template slot='title'>
                    <i class="el-icon-success"></i>
                    <span></span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item  index="1-1" @click="addQuestion('1-1','')">
                            
                        </el-menu-item>
                        <el-menu-item  index="1-2" @click="addQuestion('1-2','')">
                            
                        </el-menu-item>
                    </el-menu-item-group>
            </el-submenu>
            <el-submenu index='2' >
                <template slot='title'>
                    <i class="el-icon-edit"></i>
                    <span></span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item  index="2-1" @click="addQuestion('2-1','')">
                            
                        </el-menu-item>
                        <el-menu-item  index="2-2" @click="addQuestion('2-2','')">
                            
                        </el-menu-item>
                    </el-menu-item-group>
            </el-submenu>
            <el-submenu index='2' >
                <template slot='title'>
                    <i class="el-icon-star-off"></i>
                    <span></span>
                    </template>
                    <el-menu-item-group>
                        <el-menu-item  index="3-1" @click="addQuestion('3-1','')">
                            
                        </el-menu-item>
                    </el-menu-item-group>
            </el-submenu>
        </el-menu>
        </el-col >
        <el-col :span="20"  :offset="ColOffset">
            <el-container>
                <el-header><!---->
                    <el-card>
                      <el-input placeholder="" clearable autosize v-model="QuestionList.title"></el-input>
                      <el-input placeholder="" clearable autosize v-model="QuestionList.remark"></el-input>
                    </el-card>
                </el-header>
                <el-main><!---->              
                  <el-card  v-for="(item,key) in QuestionList.questions" :key="key" track-by="$index">
                    <component :isSend="isSend"  :is="items[key].component" :bean="key" :QuestionList.sync="QuestionList"  @listenComponentData="listenComponentData" @delete_big="delete_main" ></component>
                  </el-card>
                </el-main>
                <el-footer><!---->
                    <el-card>
                        <el-row>
                            <el-col>
                              <el-input placeholder="" clearable autosize :disabled="true"></el-input>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :offset="9" :span="3">
                              <el-button class="sharebutton" type="primary" @click="comfirm('');routerLink('/exhibition/questionnare',1)"></el-button>
                            </el-col>
                            <el-col  :span="3" :offset="1">
                              <el-button class="sharebutton" type="primary" @click="comfirm('');routerLink('/exhibition/questionnare',1)"></el-button>
                            </el-col>
                        </el-row>
                    </el-card>
                </el-footer>
            </el-container>
        </el-col>
   </el-row>
</div>
</template>


<script>
  import Singlefill from '@/components/Questionnaire/Single-fill-in-the-blank'
  import Multifill from '@/components/Questionnaire/Multiple-fill'
  import Multichoice from '@/components/Questionnaire/Multiple-choice'
  import Singlechoice from '@/components/Questionnaire/Single-choice-question'
  import Scoreproblem from '@/components/Questionnaire/Score-problem'
  import Qs from 'qs'
  import * as Survy_Api from '@/Questionnaire_interface/api.js'
  
export default {
  components:{
    Singlefill,
    Multifill,
    Multichoice,
    Singlechoice,
    Scoreproblem,
  },
    data(){
        return{
            ColOffset:0.5,//offset
            searchBarFixed:false,     //
            isSend:false,    //
            colwidth:4,
            surveyId:1,//id
            isCollapse:false,  //
            items:[],     //
            m:1,
            //
            QuestionList:{title:"",  //
            remark:"", //
            templateType:0,    //01
            status:0,          //01
            categoryIds:[1] ,   //1-12
            questions:[],
            },
            cpn:[{        //
                index:'1-1',
                comp:'Singlechoice',    //
            },
            {
                index:'1-2',
                comp:'Multichoice'
            },
            {
                index:'2-1',
                comp:'Singlefill'
            },
            {
                index:'2-2',
                comp:'Multifill'
            },
            {
                index:'3-1',
                comp:'Scoreproblem'
            }]
        }
    },
    methods:{
      addQuestion(index,text){  //
      let component;
      let serialNumber;  //
      for(let i =0;i<5;i++){
          if(this.cpn[i].index == index){
              component = this.cpn[i].comp;
          }
      }
        this.items.push({
          'component':component,  //
          'text':this.m++,            //
        });
        switch(component){    //QuestionList.questions
          case 'Singlechoice':    //
          this.QuestionList.questions.push({
              "serialNumber":1, 
              "title":"",
              "answerType":0,
              "optionType":0,
              "minNumber":1,
              "maxNumber":1,
              "page":0,              //0
              "choices":[
                {
                "serialNumber":1,         //                        0
                "title":"",   //
                "type":0,                  //:0:1
                "defaultStatus":0         //                      0
                },
                {
                "serialNumber":2,
                "title":"",
                "type":0,
                "defaultStatus":0         //                      0
                },
                  ],            //})
                });
                break;
          case 'Multichoice':   //
          this.QuestionList.questions.push({
              "serialNumber":1, 
              "title":"",
              "answerType":1,
              "optionType":0,
              "minNumber":2,
              "maxNumber":0,
              "page":0,              //0
              "choices":[
                {
                "serialNumber":1,         //                        0
                "title":"",   //
                "type":0,                  //:0:1
                "defaultStatus":0         //                      0
                },
                {
                "serialNumber":2,
                "title":"",
                "type":0,
                "defaultStatus":0         //                      0
                },
                  ],            //})
                });
                break;
          case 'Singlefill':   //
          this.QuestionList.questions.push({
              "serialNumber":1, 
              "title":"",
              "answerType":2,
              "optionType":0,
              "minNumber":1,
              "maxNumber":1,
              "page":0,              //0
              "choices":[
                {
                "serialNumber":1,         //                        0
                "title":"",   //
                "type":1,                  //:0:1
                "defaultStatus":0         //                      0
                },
                  ],            //})
                });
                break;
          case 'Multifill':   //
          this.QuestionList.questions.push({
              "serialNumber":1, 
              "title":"",
              "answerType":2,
              "optionType":0,
              "minNumber":2,
              "maxNumber":0,
              "page":0,              //0
              "choices":[
                {
                "serialNumber":1,         //                        0
                "title":"",   //
                "type":1,                  //:0:1
                "defaultStatus":0         //                      0
                },
                {
                "serialNumber":2,
                "title":"",
                "type":1,
                "defaultStatus":0         //                      0
                },
                  ],            //})
                });
                break;
          case 'Scoreproblem':   //
          this.QuestionList.questions.push({
              "serialNumber":1, 
              "title":"",
              "answerType":3,
              "optionType":0,
              "minNumber":1,
              "maxNumber":1,
              "page":0,              //0
              "choices":[],            //})
                });
                break;      
        }
      },
      delete_main(index){     //
          //this.QuestionList.questions = [];  //
          this.QuestionList.questions.splice(index,1);  //
          this.items.splice(index,1);   // 
      },
      routerLink(path,id){
        var time = false;
        setTimeout(() => {
        this.$router.push({
         path:path,    
        })
        }, 2000);  
        },
      comfirm(signal){
        if(signal == ""){
          this.QuestionList.status = 1;
        }else if(signal == ""){
          this.QuestionList.status = 0;
        }
        var that = this;
          this.$confirm('','',{
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
          }).then(() => {
                that.isSend = !that.isSend;   //
                console.log(this.QuestionList);
                //var data =JSON.stringify(this.QuestionList);
                if(localStorage.getItem("XSurveyChange") == ""){
                  Survy_Api.default.updateSurvey(that,this.QuestionList);
                }else if(localStorage.getItem("XSurveyChange") == ""){
                  Survy_Api.default.addSurvey(that,this.QuestionList);
                }

                const loading = this.$loading({ 
                  lock: true, 
                  text: '', 
                  spinner: 'el-icon-loading', 
                  background: 'rgba(0, 0, 0, 0.7)', 
                  target: document.querySelector('.div1') 
                }); 
                setTimeout(() => {
                  loading.close();
                  
                }, 2000); 
                
/*                 that.$http.post(vm.testUrl,data, {headers:{'Content-Type':'application/x-www-form-urlencoded'}}).then((response)=>{
                console.log(response.data.msg);
                }).then((response)=>{
                  if(response.data.result == 'success'){
                    this.$message({
                        type: 'success',
                        message: ''
                    })
                  }else if(response.data.result == 'error'){
                    this.$message({
                        type: 'error',
                        message: ''
                    })  
                  }
                }).catch((response) =>{
                       this.$message({
                        type: 'error',
                        message: 'qaq'
                    }) 
                    }) */

             
          }).catch(() => {
              this.$message({
                  type: 'info',
                  message: ''
              })
          })
      },
      listenComponentData(childArray){
        this.QuestionList.push({  //

        })
      },
      init(){     //localstorage
        this.QuestionList = JSON.parse(localStorage.getItem("XSurveyInformation"));
        let component;
        let serialNumber;  //
        for(let i =0;i<this.QuestionList.questions.length;i++){
            if(this.QuestionList.questions[i].answerType ==0){
              component = "Singlechoice";
            }else if(this.QuestionList.questions[i].answerType ==1){
              component = "Multichoice";
            }else if(this.QuestionList.questions[i].answerType ==3){
              component = "Scoreproblem";
            }else if(this.QuestionList.questions[i].answerType ==2&&this.QuestionList.questions[i].choices.length == 0){
              component = "Singlefill";
            }
            else if(this.QuestionList.questions[i].answerType ==2&&this.QuestionList.questions[i].choices.length != 0){
              component = "Multifill";
            };
            this.items.push({
            'component':component,  //
            'text':this.m++,            //
          });
        } 
        },
        handleScroll () {         //
          var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
          console.log(scrollTop);
          var offsetTop = document.getElementById("MenuList").offsetTop
          if (scrollTop > 336) {   //
            this.searchBarFixed = true;
            this.ColOffset = 4;
          } else {
            this.searchBarFixed = false;
            this.ColOffset = 0.5;
          }
        },
    },
    activated(){
      this.init();
    },
    watch:{
      QuestionList:{
        handler(){
          for(let i = 0;i<this.QuestionList.questions.length;i++){
            this.QuestionList.questions[i].serialNumber = i+1;
            if(this.QuestionList.questions[i].choices.length != 0){
              for(let k = 0;k<this.QuestionList.questions[i].choices.length;k++){
                this.QuestionList.questions[i].choices[k].serialNumber = k+1;
                  }
            }
          }
        localStorage.setItem("XSurveyInformation",JSON.stringify(this.QuestionList));
        //this.QuestionList = JSON.parse(localStorage.getItem("XSurveyInformation"));
      },
      deep: true}
    },
    mounted () {
      window.addEventListener('scroll', this.handleScroll);     //
    },
}
</script>


<style scoped lang="scss">
    .isFixed{
      position:fixed;
      top:0;
      z-index:999;
    }
    #QuestionnareEditing{
      background-color:rgba(128, 133, 144, 0.72);
    }
    .el-menu:not(.el-menu--collapse){
        width:200px;
        min-height:400px;
    }
    .el-input{
        margin:8px 0;
    }
    .el-header,.el-main,.el-footer{
        //margin:20px 0;
        height:100%!important;
    }
    .el-header{
        .el-card{
            margin:4px 0 20px 0;
        }
    }
    .el-main{
      min-height: 200px;
      //background: rgba(128, 133, 144, 0.56);
      padding:0 20px!important;
    }
    .sharebutton{

    }
    .el-card{
        margin:20px 0;
        padding:0;
    }
    .Istoggle{
        
        height:26px;
        color:white;
        line-height: 30px;
        text-align: left;
        i{
            background:#409eff;
            width:55px;
            padding:5px;
            text-align: center;
        }
        
    }
    .el-icon-arrow-down{
        width:193px!important;
    }

</style>


