<template>
  <div id="met-sign">
    <div class="left_block">
      <!--  -->
      <div class="box1">
        <div class="curr_position">
          
          <a @click="selectItem('/home')"></a>
          <span>></span>
          <a>()-</a>
        </div>
      </div>
      <!--  -->
      <div class="box3">
        <ul class="about">
          <li class="first"><span>{{data.holdTime}}</span></li>
          <!-- <li><span></span></li>
          <li style="width:23s%;border-right:none;"> <span></span></li> -->
        </ul>
        <ul class="itme">
          <li class="first"><span>{{data.title}}</span></li>
          <li class="job-compony"><span>{{data.companyName}}</span></li>
          <li class="job-width"><span>{{data.companySize}}</span></li>
          <li class="job-class"><span>{{data.companyNature}}</span></li>
          <li class="job-type"><span>{{data.industry}}</span></li>
        </ul>
        <ul class="pay">
          <li>
            <div><span style="color:#fd5f39;">{{data.salary}}</span></div>
          </li>
          <li>
            <div><span>{{data.positionName}}</span></div>
          </li>
          <li>
            <div><span>{{data.degreeRequired}}</span></div>
          </li>
          <li>
            <div><span>{{data.hiring}}</span></div>
          </li>
          <li><span>{{data.workAge}}</span></li>
          <li><span>{{data.phone}}</span></li>
          <div class="supply"></div>
          <span></span>
        </ul>
        <ul class="job-title">
          <li
            :class="{active2:tab.isActive}"
            v-for="(tab,index) in tabs"
            v-if="tab.belong === ''"
            :key="tab.name"
            @mouseover="tabLink(index,tab.tocpn)"
          >
            <a>{{tab.name}}</a>
          </li>
        </ul>
        <div
          class="job-content"
          style="width:100%;"
        >
          <div v-html="data.jobDescription"></div>
        </div>
        <div class="job-more">

        </div>
        <div>
        </div>
      </div>
    </div>
    <div class="right_block">
      <!--  -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;"></a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
      <!--  -->
      <div class="box4">
        <ul
          class="title"
          style="margin-left:15px;"
        >
          <li
            class="list_title"
            v-for="(tab,index) in tabs"
            v-if="tab.belong === ''"
            :key="tab.name"
            @mouseover="tabLink(index,tab.tocpn)"
          >
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[6]"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!--  -->
      <div class="box4">
        <ul
          class="title"
          style="margin-left:15px;"
        >
          <li
            class="list_title"
            v-for="(tab,index) in tabs"
            v-if="tab.belong === ''"
            :key="tab.name"
            @mouseover="tabLink(index,tab.tocpn)"
          >
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[7]"></pb-list>
          </keep-alive>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NormalList from "@/components/common/normal-list";
import Banning from "@/components/common/banning";

export default {
  name: "met-asign",
  data() {
    return {
      currentTab: [
        "pb-list",
        "pb-list",
        "pb-list2",
        "pb-list",
        "pb-list3",
        "pen-list",
        "",
        ""
      ], //
      tabs: [
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "", //
          content: null, //
          isActive: true
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "normal-list", //
          content: null, //
          isActive: false
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "banning", //
          content: null, //
          isActive: false
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "banning", //
          content: null, //
          isActive: true
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "banning", //
          content: null, //
          isActive: false
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "banning", //
          content: null, //
          isActive: false
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: null, //
          content: null, //
          isActive: true
        },
      ],
      data: {
        id: 1,
        userId: null,
        coverImg: "sdfa",
        title: "adfa",
        holdTime: "2018-11-22",
        address: null,
        host: null,
        introduction: null,
        type: null
      }
    };
  },
  methods: {
    selectItem(item) {
      this.$emit("select", item);
    }, //itemselect
    // routerLink(path){
    //   this.$router.push(path);//
    // },
    tabLink(index, tocpn) {
      //
      this.$set(
        this.tabs,
        this.tabs[index],
        (this.tabs[index].isActive = true)
      );
      for (var i = 0; i < this.tabs.length; i++) {
        if (this.tabs[i].belong == this.tabs[index].belong && i != index) {
          this.$set(this.tabs, this.tabs[i], (this.tabs[i].isActive = false)); //methodsdata
        }
      }
      if (tocpn != null) {
        var t = 6;
        if (this.tabs[index].belong == "") t = 6;
        else if (this.tabs[index].belong == "") t = 7;

        this.currentTab[t] = this.tabs[index].tocpn;
        //
      }
    },
    init() {
      // let id = JSON.parse(this.$route.query.item); //
      // let that = this;
      // let url = that.HOST + "/position/getPositionInfo";
      // that.axios
      //   .get(url, {
      //     params: { id: id }
      //   })
      //   .then(response => {
      //     that.data = response.data.message;
      //     that.data.createTime = that.data.createTime.slice(0, 10);
      //   })
      //   .catch(response => {
      //     that.$message.error({
      //       showClose: true,
      //       message: "",
      //       duration: 2000
      //     });
      //   });
    }
  },
  activated() {
    this.init();
  },
  components: {
    NormalList,
    Banning
  }
};
</script>

<style lang="scss" scoped>
@mixin left {
  margin: 5px auto;
  width: 795px;
  float: left;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin right {
  margin: 5px auto;
  width: 390px;
  float: right;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin i-box {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.95);
}
.active {
  color: #127de7 !important;
  border-bottom: 2px solid #127de7;
  padding-bottom: 4px;
}
.active2 {
  & {
    box-sizing: border-box;
    color: #535353;
    font-weight: 600;
  }
  &::after {
    content: "";
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 100%;
    height: 4px;
    border-bottom: #127de7 3px solid;
    z-index: 99;
  }
}
#met-sign {
  margin: 5px auto;
  width: 1200px;
  overflow: hidden;
}
.left_block {
  @include left;
}
.right_block {
  @include right;
}
.box1,
.box2,
.box3 {
  @include left;
  @include i-box;
}
.box1 {
  & {
    color: #404040;
    font-weight: 500;
    padding: 0 15px;
    overflow: hidden;
  }
  .curr_position {
    & {
      margin-top: 14px;
      margin-bottom: 15px;
    }
    a {
      text-decoration: none;
      cursor: pointer;
    }
  }
}
.box3 {
  color: #404040;
  padding: 0 15px;
}
@mixin class1 {
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
  margin: 5px 0;
  padding: 10px 0;
  border-bottom: #ccc 3px solid;
}
.about {
  & {
    @include class1;
  }
  .first {
    margin-left: 5px;
  }
  li {
    text-align: center;
    display: inline-block;
    box-sizing: border-box;
    padding-right: 15px;
    line-height: 15px;
  }
}
.itme {
  & {
    @include class1;
    padding: 5px 0 10px 0;
  }
  .first {
    font-weight: 500;
    display: block;
    font-size: 36px;
    color: #333;
    margin: 0 0 8px;
  }
  .job-compony {
    display: block;
  }
  li {
    text-align: left;
    padding: 3px 5px;
    display: inline-block;
    box-sizing: border-box;
    padding-right: 35px;
  }
}
.pay {
  & {
    @include class1;
    border-bottom: none;
  }
  li {
    & {
      text-align: left;
      padding: 5px 8px;
      box-sizing: border-box;
      padding-right: 90px;
    }
    span {
      padding: 10px;
    }
  }
  .supply {
    display: inline-block;
    color: #fff;
    background: #127de7;
    font-weight: 500;
    font-size: 18px;
    padding: 10px 13px;
    margin: 10px 0;
    border-radius: 5px;
    cursor: pointer;
  }
}
.job-title {
  & {
    @include class1;
    overflow: initial;
    padding: 0;
  }
  li {
    & {
      font-size: 20px;
      display: inline-block;
      text-align: left;
      padding-bottom: 10px;
      font-weight: 500;
      padding:0 10px 10px;
      margin-right: 20px;
      color: #404040;
      cursor: pointer;
      user-select: none;
      position: relative;
    }
    // &:hover {
    //   box-sizing: border-box;
    //   color: #535353;
    //   font-weight: 600;
    // }
    // &:hover::after {
    //   content: "";
    //   position: absolute;
    //   bottom: -3px;
    //   left: 0;
    //   width: 100%;
    //   height: 4px;
    //   border-bottom: #127de7 3px solid;
    //   z-index: 99;
    // }
    a {
      text-align: left;
    }
  }
}
.job-content {
  @include i-box;
  height: 200px;
  margin-top: 20px;
  padding: 0 10px;
}
.box4 {
  @include right;
  @include i-box;
}
.title {
  margin-bottom: 12px;
  margin-top: 2px;
  width: 100%;
  height: 28px;
  line-height: 28px;
}
.title li {
  float: left;
}
.list_title a {
  color: #7b7b7b;
  cursor: default;
  font-size: 1em;
  font-weight: 500;
  line-height: 25px;
  vertical-align: middle;
  margin-right: 10px;
}
.list_title a:hover {
  color: #127de7;
  border-bottom: 2px solid #127de7;
  cursor: default;
  padding-bottom: 4px;
}
</style>


