<template>
    <div id="Scoreproblem">
      <el-card shadow="hover">
        <div slot="header" class="clearfix">
          <span>{{bean+1}}</span>
        </div>
        <el-row>
          <el-col :span="22">
            <el-input placeholder="" v-model="QuestionList.questions[bean].title">
            </el-input>
          </el-col>
          <el-col :span="1" :offset="1">
            <el-button type="text" class='fa fa-trash-o fa-2x'  @click="delete_all"></el-button>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <!-- <i class="el-icon-star-on fa-2x"></i>
          </el-col>
          <el-col :offset="1" :span="1">
            <i class="el-icon-star-on fa-2x"></i>
          </el-col>
          <el-col :offset="1" :span="1">
            <i class="el-icon-star-on fa-2x"></i>
          </el-col>
          <el-col :offset="1" :span="1">
            <i class="el-icon-star-on fa-2x"></i>
          </el-col>
          <el-col :offset="1" :span="1">
            <i class="el-icon-star-on fa-2x"></i> -->
            <el-rate disabled text-color="#ff9900" v-model="valueStar"></el-rate>
          </el-col>
        </el-row>
      </el-card>
    </div>
</template>
<script>
export default {
    data(){
      return{
        valueStar:5,   //
        question:{
                        "serialNumber":1,     //                              0
                        "title":"", //
                        "answerType":3,       //0123
                        "optionType":0,       //01
                        "minNumber":1,        //    0
                        "maxNumber":5,        //    0
                        "page":0,              //0
                        //"precedentChoiceId":1,//id,                            ****
                        //"precedentQuestionId":, //id                          ****
                        "choices":            //
                        [
                            
                        ]
            }
      }
      
    },
    props:{
          bean:{

          },
          isSend:{

          },
          QuestionList:{

          },
      },
    methods:{
      delete_all(){   //
        this.$emit('delete_big',this.bean)
      }
    },
    watch:{
        isSend:function(){
            /* this.question.serialNumber = this.bean+1;
            this.question.answerType = 3;
            this.QuestionList.questions.push(this.question); */
        },
        QuestionList:function(){
            this.QuestionList.questions[this.bean].serialNumber = this.bean + 1;
            this.QuestionList.questions[this.bean].answerType = 3;
        }
    }
}
</script>
<style scoped lang='scss'>
.el-card{
    padding:25px;
  }
.el-input{
    margin:10px 0;
  }
  .el-row{
    margin:8px 0;
  }
</style>


