<template>
    <div id="Singlefill">
        <el-card shadow="hover">
            <div slot="header" class="clearfix">
              <span>{{bean+1}}</span>
            </div>
            <el-row>
                <el-col class="SinglefillInput" :span="22">
                    <el-input placeholder=""   v-model="QuestionList.questions[bean].choices[0].title">
                    </el-input>
                </el-col>
                <el-col :span="1" :offset="1">
                   <el-button type="text" class='fa fa-trash-o fa-2x' @click="delete_all"></el-button>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-input :disabled="true">
                    </el-input>
                </el-col>
            </el-row>
        </el-card>
    </div>
</template>
<script>
export default {
    data(){
        return{
              question:{
                        "serialNumber":1,     //                              0
                        "title":"", //
                        "answerType":2,       //0123
                        "optionType":0,       //01
                        "minNumber":1,        //    0
                        "maxNumber":1,        //    0
                        "page":0,              //0
                        //"precedentChoiceId":1,//id,                            ****
                        //"precedentQuestionId":, //id                          ****
                        "choices":            //
                        [
                            
                        ]
            }
        }
    },
    props:{
        bean:{

        },
        isSend:{

        },
        QuestionList:{

        },
    },
    methods:{
          delete_all(){   //
            this.$emit('delete_big',this.bean);
            }
    },
    watch:{
        isSend:function(){
            /* this.question.serialNumber = this.bean+1;
            this.question.answerType = 2;
            this.QuestionList.questions = this.question; */
        },
        QuestionList:function(){
            this.QuestionList.questions[this.bean].serialNumber = this.bean + 1;
            this.QuestionList.questions[this.bean].answerType = 2; 
            console.log(this.QuestionList);
        }
    }
 
}
</script>
<style scoped lang='scss'>
  .SinglefillInput{
    height:50px;
  }
 .el-card{
    padding:25px;
  }
.el-input{
    margin:10px 0;
  }
</style>


