<template>
  <div id="exhibition-default">
    <div class="left_block">
      <!--  -->
      <div class="box1">
        <div class="curr_position">
          
          <a @click="selectItem('/home')"></a>
          <span>></span>
          <a @click="selectItem('/exhibition')"></a>
        </div>
      </div>
      <!--  -->
      <div class="box2">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;"></a>
          </li>
          <span class="more" @click="selectItem('')">-></span>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <meeting-dynamic></meeting-dynamic>
          </keep-alive>
        </div>
      </div>
      <!--  -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;"></a>
          </li>
        </ul>
        <div style="width:100%;">
          <meeting-item :sign="true" @displaySign="displaySign" style="margin-bottom:20px;" v-for="(item,index) in tabMessage[1].slice((currentPage[0]-1)*pageSize[0],currentPage[0]*pageSize[0])" :key="item.id" :my-message="tabMessage[1][index+(currentPage[0]-1)*pageSize[0]]" :current-tab="currentTab[1]"></meeting-item>
          <el-pagination background layout="prev, pager, next" :total="parseInt(total[0])" :page-size="pageSize[0]" :current-page.sync="currentPage[0]">
          </el-pagination>
        </div>
      </div>
      <!--  -->
      <div class="box3">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;"></a>
          </li>
        </ul>
        <div style="width:100%;">
          <meeting-item :sign="false" style="margin-bottom:20px;" v-for="(item,index) in tabMessage[2].slice((currentPage[1]-1)*pageSize[1],currentPage[1]*pageSize[1])" :key="item.id" :my-message="tabMessage[2][index+(currentPage[1]-1)*pageSize[1]]" :current-tab="currentTab[2]"></meeting-item>
          <el-pagination background layout="prev, pager, next" :total="parseInt(total[1])" :page-size="pageSize[1]" :current-page.sync="currentPage[1]" @current-change="currentChange()">
          </el-pagination>
        </div>
      </div>
    </div>
    <div class="right_block">
      <!--    -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="active"></a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
      <!--  -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === ''" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[4].cpn"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!--  -->
      <div class="box4">
        <ul class="title" style="margin-left:15px;">
          <li class="list_title" v-for="(tab,index) in tabs" v-if="tab.belong === ''" :key="tab.name" @mouseover="tabLink(index,tab.tocpn)">
            <a :class="{active:tab.isActive}">{{tab.name}}</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <pb-list :is="currentTab[5].cpn"></pb-list>
          </keep-alive>
        </div>
      </div>
      <!--  -->
      <div class="box4">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="active"></a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <!-- <advertise-list></advertise-list> -->
          </keep-alive>
        </div>
      </div>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="signVisible" :close-on-click-modal="true" :modal-append-to-body="false" top="8vh" v-loading="loading" element-loading-text="">
      <el-form :model="signForm" label-width="100px" :rules="signFormRules" ref="signForm">
        <el-form-item label="" prop="name">
          <el-input v-model="signForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="gender">
          <el-input v-model="signForm.gender" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="signForm.phone" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="weChat">
          <el-input v-model="signForm.weChat"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="signForm.email" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companyName">
          <el-input v-model="signForm.companyName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="position">
          <el-input v-model="signForm.position" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companyAddress">
          <el-input v-model="signForm.companyAddress" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="signVisible = false"></el-button>
        <el-button type="primary" @click.native="signSubmit"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import NormalList from "@/components/common/normal-list";
import MeetingDynamic from "@/components/common/meeting-dynamic";
import MeetingItem from "@/components/common/meeting-item";
import Banning from "@/components/common/banning";
import Qs from "qs";

export default {
  name: "exhibition-default",
  data() {
    return {
      //,
      tabMessage: [
        "",
        [
          {
            id: 1,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address1",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 2,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address2",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 3,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address3",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 4,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address4",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 5,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address5",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 6,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address6",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 7,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address7",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 8,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address8",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 9,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address9",
            host: "host", //
            introduction: "message..." //
          }
        ],
        [
          {
            id: 1,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address1",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 2,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address2",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 3,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address3",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 4,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address4",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 5,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address5",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 6,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address6",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 7,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address7",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 8,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address8",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 9,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address9",
            host: "host", //
            introduction: "message..." //
          },
          {
            id: 10,
            coverImg: null, //
            title: "",
            holdTime: "",
            address: "address123213",
            host: "host", //
            introduction: "message..." //
          }
        ],
        "",
        ""
      ], //
      //
      currentTab: [
        {
          name: "",
          list: "",
          cpn: "meeting-dynamic"
        },
        {
          name: "",
          list: "",
          cpn: "meeting-item"
        },
        {
          name: "",
          list: "",
          cpn: "meeting-item"
        },
        {
          name: "",
          list: "",
          cpn: ""
        },
        {
          name: "",
          list: "",
          cpn: "normal-list",
          newsUrl: ""
        },
        {
          name: "",
          list: "",
          cpn: "normal-list",
          newsUrl: ""
        }
      ],
      tabs: [
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "", //
          content: null, //
          isActive: true
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "normal-list", //
          content: null, //
          isActive: false
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "banning", //
          content: null, //
          isActive: false
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "banning", //
          content: null, //
          isActive: true
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "banning", //
          content: null, //
          isActive: false
        },
        {
          name: "",
          belong: "", //
          path: null, //
          tocpn: "banning", //
          content: null, //
          isActive: false
        }
      ],
      //
      isRequested: false,
      total: [9, 10], // 
      pageSize: [3, 5], // 
      currentPage: [1, 1], //
      // listFirst: 0
      signVisible:false,
      signForm:{
        conferenceId:'',
        name:"",
        gender:'',
        phone:'',
        weChat:'',
        email:'',
        companyName:'',
        position:'',
        companyAddress:""
      },
      signFormRules:{
        name:[{ required: true, message: "", trigger: "blur" }],
        gender:[{ required: true, message: "", trigger: "blur" }],
        phone:[{ required: true, message: "", trigger: "blur" }],
        weChat:[{ required: false, message: "", trigger: "blur" }],
        email:[{ required: true, message: "", trigger: "blur" }],
        companyName:[{ required: true, message: "", trigger: "blur" }],
        position:[{ required: false, message: "", trigger: "blur" }],
        companyAddress:[{ required: false, message: "", trigger: "blur" }],
      },
      loading:false
    };
  },
  // watch: {
  //   currentPage(oldV, newV) {
  //     console.log(oldV, newV);
  //   }
  // },
  methods: {
    // currentChange() {
    //   this.listFirst = (this.currentPage[1] - 1) * 5;
    //   console.log(this.listFirst);
    // },
    // routerLink(path) {
    //   this.$router.push(path); //
    // },
    displaySign(id){
      this.signVisible=true;
      this.signForm.conferenceId=id;
    },
    selectItem(path, data) {
      let item = JSON.stringify(data);
      this.$emit("select", path, item);
    }, //itemselect

    tabLink(index, tocpn) {
      //
      this.$set(
        this.tabs,
        this.tabs[index],
        (this.tabs[index].isActive = true)
      );
      for (var i = 0; i < this.tabs.length; i++) {
        if (this.tabs[i].belong == this.tabs[index].belong && i != index) {
          this.$set(this.tabs, this.tabs[i], (this.tabs[i].isActive = false)); //methodsdata
        }
      }
      var t = 6;
      if (this.tabs[index].belong == "") t = 6;
      else if (this.tabs[index].belong == "") t = 7;

      this.currentTab[t] = this.tabs[index].tocpn;
      //
    },
    init() {
      var a = 0,
        b = 0;
      var url = this.HOST + "/conference/getRelevantConference";
      //
      this.axios
        .get(url, {
          params: {
            relevantId: 0
          }
        })
        .then(response => {
          console.log(response.data.result);
          //
          this.tabMessage[1] = response.data.message;
          this.total[0] = this.tabMessage[1].length;
          a = 1;
          this.isRequested = true; //
        })
        .catch(function(response) {
          console.log(response);
        });
      //
      this.axios
        .get(url, {
          params: {
            relevantId: -1
          }
        })
        .then(response => {
          console.log(response.data.result);
          //
          this.tabMessage[2] = response.data.message;
          this.total[1] = this.tabMessage[2].length;
          b = 1;
        })
        .catch(function(response) {
          console.log(response);
        });
      if (a && b) {
        this.isRequested = true; //
      }
    },
    //
    signSubmit(){
      let that = this;
      let para = Object.assign({}, that.signForm);
      // console.log(para)
      if (para.name && para.gender && para.phone&&para.email&&para.companyName) {
        that.$confirm("", "", { type: "warning" }).then(() => {
          let url = that.HOST + "/conference/attend";
          let params = Qs.stringify({
            conferenceId:that.signForm.conferenceId,
            name:para.name,
            gender:para.gender,
            phone:para.phone,
            weChat:para.weChat,
            email:para.email,
            companyName:para.companyName,
            position:para.position,
            companyAddress:para.companyAddress
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "",
                  duration: 1500
                });
                that.signVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.signVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "",
          duration: 1000
        });
      }
    }
  },
  components: { NormalList, MeetingDynamic, MeetingItem,
    Banning }
};
</script>

<style lang="scss" scoped>
@mixin left {
  margin: 5px auto;
  width: 795px;
  float: left;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin right {
  margin: 5px auto;
  width: 390px;
  float: right;
  box-sizing: border-box;
  overflow: hidden;
}
@mixin i-box {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.95);
}
.active {
  color: #127de7 !important;
  border-bottom: 2px solid #127de7;
  padding-bottom: 4px;
}
#exhibition-default {
  margin: 5px auto;
  width: 1200px;
  overflow: hidden;
}
.left_block {
  @include left;
}
.right_block {
  @include right;
}
.box1,
.box2,
.box3 {
  @include left;
  @include i-box;
}
.box3 {
  margin-bottom: 30px;
}
.box1 {
  & {
    color: #404040;
    font-weight: 500;
    padding: 0 15px;
    overflow: hidden;
  }
  .curr_position {
    & {
      margin-top: 14px;
      margin-bottom: 15px;
    }
    a {
      text-decoration: none;
      cursor: pointer;
    }
  }
}
.box4 {
  @include right;
  @include i-box;
}
.title {
  margin-bottom: 12px;
  margin-top: 2px;
  width: 100%;
  height: 28px;
  line-height: 28px;
}
.title li {
  float: left;
}
.more {
  & {
    color: #666;
    font-size: 14px;
    float: right;
    padding: 2px 10px 0 2px;
    cursor: pointer;
  }
  &:hover {
    color: #127de7;
  }
}

.list_title a {
  color: #7b7b7b;
  cursor: default;
  font-size: 1em;
  font-weight: 500;
  line-height: 25px;
  vertical-align: middle;
  margin-right: 10px;
}
.list_title a:hover {
  color: #127de7;
  border-bottom: 2px solid #127de7;
  cursor: default;
  padding-bottom: 4px;
}
</style>



