<template>
    <div id="Multiplechoice">
        <el-card shadow="hover">
          <div slot="header" class="clearfix">
              <span>{{bean+1}}</span>
          </div>
          <el-row>
            <el-col :span="22">
            <el-input placeholder="" clearable autosize v-model="QuestionList.questions[bean].title">
            </el-input>
            </el-col>
            <el-col :span="1" :offset="1">
                   <el-button type="text" class='fa fa-trash-o fa-2x'  @click="delete_all"></el-button>
            </el-col>
          </el-row> 
          <el-row v-for="(item,key) in QuestionList.questions[bean].choices" :key="key">
            <Option @deletive="deleting"  :beanPrev="bean" :QuestionList.sync="QuestionList"  :bean="key" :isSend="isSend" :optionList.sync="question">
            </Option>
          </el-row>
          <el-row>
            <el-button type="text" icon="el-icon-plus" @click="addSinglechoice"></el-button>
          </el-row> 
        </el-card>
    </div>
</template>
<script>
import Option from '@/components/Questionnaire/Question-option'
export default {
    props:{
        bean:{

        },
        isSend:{

        },
        QuestionList:{

        }
    },    
    components:{
    Option
  },
data(){
  return{
    items:[1,1],
    question:{
              "serialNumber":1,     //                              0
              "title":"", //
              "answerType":1,       //0123
              "optionType":0,       //01
              "minNumber":2,        //    0
              "maxNumber":0,        //    0
              "page":0,              //0
              //"precedentChoiceId":1,//id,                            ****
              //"precedentQuestionId":1, //id                          ****
              "choices":            //
              [
                   
              ]
              },
  }
},
methods:{
deleting(index){   //
    //this.items.splice(index,1);    //
    this.QuestionList.questions[bean].choices.splice(index,1); 
  },
  delete_all(){   //
  //console.log(this.bean)
        this.$emit('delete_big',this.bean)
  },
  addSinglechoice(){    //
    //this.items.splice(this.items.length-1,0,1) ;
    this.QuestionList.questions[bean].choices.push({
                                                        "serialNumber":1,         //                        0
                                                        "title":"",   //
                                                        "type":0,                  //:0:1
                                                        "defaultStatus":0         //                      0
                                                    })
  }
},
watch:{
  isSend:function(){
    /* this.question.serialNumber = this.bean+1;
    this.question.maxNumber = this.items.length;
    this.QuestionList.questions.push(this.question); */
  }
}
}
</script>
<style scoped lang='scss'>
.el-input{
    margin:10px 0;
  }
  .el-card{
    padding:25px;
  }
</style>


